<template>
  <div class="container panel">
    <h1 class="panel-heading is-4 has-text-centered">
      Propor um evento
    </h1>
    <div
      class="box"
      style="border-top-left-radius: 0px; border-top-right-radius: 0px;"
    > 
      <!-- Nome do evento -->
      <div class="field is-horizontal"> 
        <div class="field-label">
          <label
            class="label required"
            for="name"
          >Nome do evento</label>
        </div>
        <div class="field-body control">
          <input
            id="name"
            class="input is-hovered"
            type="text"
            placeholder="ex. Palestra sobre proteção Vida Marinha"
            required
          >
        </div>
      </div>
      <!-- Tipo do evento -->
      <div class="field is-horizontal">
        <div class="field-label">
          <label
            class="label required"
            for="type"
          >Tipo do evento</label>
        </div>
        <div class="field-body">
          <div class="control">
            <div class="select">
              <select
                id="type"
                required
              >
                <option />
                <option>Tipo de evento 1</option>
                <option>Tipo de evento 2</option>
              </select>
            </div>
          </div>
        </div>
      </div>
      <!-- Descrição do evento -->
      <div class="field is-horizontal">
        <div class="field-label is-normal">
          <label
            class="label required"
            for="description"
          >Descrição</label>
        </div>
        <div class="field-body">
          <div class="field">
            <div class="control">
              <textarea
                id="description"
                class="textarea"
                placeholder="Palestra sobre proteção da vida marinha projetada para pessoas com conhecimentos na àrea da biologia marinha(alunos, professores, investigadores, etc)"
                required
              />
            </div>
          </div>
        </div>
      </div>
      <!-- Data de realização do evento -->
      <div class="field is-horizontal">
        <div class="field-label is-normal">
          <label
            class="label required"
            for="begin"
          >Início</label>
        </div>
        <div class="field-body">          
          <input
            id="begin"
            class="input is-hovered"
            type="datetime-local"
            required
          >
        </div>
      </div>
      <div class="field is-horizontal">
        <div class="field-label is-normal">
          <label
            class="label required"
            for="end"
          >Fim</label>
        </div>
        <div class="field-body">          
          <input
            id="end"
            class="input is-hovered"
            type="datetime-local"
            required
          >
        </div>
      </div>

      <Submit :cancel-text="'Apagar'" />
    </div>
  </div>
</template>

<script>
import Submit from "@/components/Submit";
import { API } from "@/http-common";
export default {
  name: "CreateEvent",
  components : {
    Submit
  },
};
</script>

